<template>
  <div style="float: right">
    <el-popover  ref="popover"  placement="top-end"  :width="width"  trigger="click" :content="text">
      <slot></slot>
    </el-popover>
    <el-button icon="information" type="info"  v-popover:popover>帮助</el-button>
  </div>
</template>
<script>
    export default{
        props:{
            text:{
                String,default:"这是帮助内容"
            },
            width:{
                Number,default:200
            }
        },
        data(){
            return {}
        }
    }
</script>



